*{box-sizing:border-box;transition:0.2s ease-in-out}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}a{text-decoration:none}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

header.header{background-color:white;width:100%;height:15vh;display:flex;align-items:flex-end;justify-content:center;position:fixed;top:0;z-index:1000}header.header>span.lamp_wire{position:absolute;top:0;right:35px;width:2px;height:calc(10vh + 5px);padding:0;background-color:#0e2544}@media (min-width: 767px){header.header>span.lamp_wire{right:65px}}header.header>.lamp_icon,header.header .light_icon{position:absolute;top:10vh;right:20px;color:#FFE566;font-size:2em;z-index:1100;cursor:pointer}@media (min-width: 767px){header.header>.lamp_icon,header.header .light_icon{right:50px}}header.header>.dark_icon{color:#4d4d4d;transform:rotate(180deg)}header.header>a img{display:flex;width:200px;height:100px}@media (min-width: 767px){header.header>a img{width:300px;height:150px}}header.header_darkMode{background-color:#0e2544}header.header_darkMode>span.lamp_wire_darkMode{background-color:#4d4d4d}

footer.footer{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;width:100%;position:fixed;bottom:0;z-index:1000;border-radius:4px 4px 0 0;background:linear-gradient(45deg, #0e2544, #f22a42);background-size:250% 300%;-webkit-animation:g_color_background 5.5s ease infinite;animation:g_color_background 5.5s ease infinite}footer.footer>span{color:white;font-size:0.8em;width:100%;padding:0.5em;display:flex;justify-content:center;align-items:center}@media (min-width: 767px){footer.footer>span{font-size:1.2em}}footer.footer>div.networks_wrapper{color:white;display:flex;justify-content:center;align-items:center;font-size:0.8em;padding:0.5em}@media (min-width: 767px){footer.footer>div.networks_wrapper{font-size:1.2em}}footer.footer>div.networks_wrapper>a .net_icon{margin:0 0.5em;color:white;cursor:pointer}footer.footer>div.networks_wrapper>a .net_icon:hover{color:#2196f3}

div.carousel_wrapper{width:100%;height:100%;display:flex;flex-wrap:wrap;align-items:center;position:relative;justify-content:space-between;padding:0 10px}div.carousel_wrapper>span.arrow{width:10%;display:flex;cursor:pointer;color:#0e2544}div.carousel_wrapper>span.arrow:hover{color:#2196f3;transition:0.1s ease-in-out}div.carousel_wrapper>img{width:70%;height:70%;position:relative;bottom:0.9em}div.carousel_wrapper>img:hover{width:80%;height:80%}div.carousel_wrapper>div.btn_wrapper{width:100%;display:flex;justify-content:center;align-items:center;position:absolute;bottom:0;left:0}div.carousel_wrapper>div.btn_wrapper span.btn{width:1.5em;height:1.5em;border-radius:50%;display:flex;align-items:center;justify-content:center;padding:5px;margin:0 4px;cursor:pointer}div.carousel_wrapper>div.btn_wrapper span.btn>span.inner_btn{width:0.45em;height:0.45em;border-radius:50%;background-color:#0e2544}div.carousel_wrapper>div.btn_wrapper span.btn>span.selected{background-color:#2196f3}div.carousel_darkMode{background-color:white}

div.glasses_wrapper{width:100%;display:flex;align-items:center;justify-content:center;flex-wrap:wrap;z-index:700;position:relative;top:30vh}div.glasses_wrapper>div.head_line_wrapper{width:100%;display:flex;justify-content:center;align-items:center;position:fixed;top:15vh;z-index:1000;background-color:white;padding:5vh 0 0 0}div.glasses_wrapper>div.head_line_wrapper>span.head_line{color:#f22a42;font-size:1.2em;padding:2em}@media (min-width: 600px){div.glasses_wrapper>div.head_line_wrapper>span.head_line{font-size:1.5em}}@media (min-width: 767px){div.glasses_wrapper>div.head_line_wrapper>span.head_line{font-size:1.5em;font-weight:bold}}div.glasses_wrapper>div.head_line_wrapper_dark{background-color:#0e2544}div.glasses_wrapper>div{display:flex;justify-content:center;align-items:center;position:relative;top:7vh}@media (min-width: 767px){div.glasses_wrapper>div{top:5vh}}div.glasses_wrapper>div>button.btn{color:#2196f3;border:thin solid #2196f3;background-color:white;border-radius:20px;padding:5px 20px;margin:1em;font-size:1em;z-index:1000}div.glasses_wrapper>div>button.darkMode_btn{color:#2196f3;border:thin solid #2196f3;background-color:#0e2544}div.glasses_wrapper>div>button:hover{color:white;border:thin solid white;background-color:#2196f3;font-size:1.02em;font-weight:bold}div.glasses_wrapper>div.glasses_list{width:100%;display:flex;flex-wrap:wrap;justify-content:center;background-color:white;padding-bottom:10vh}@media (min-width: 1200px){div.glasses_wrapper>div.glasses_list{padding-bottom:0}}div.glasses_wrapper>div.glasses_list>div.item{display:flex;flex-wrap:wrap;flex-direction:column;align-items:center;width:270px;height:270px;background-color:white;margin:35px;border-radius:4px;box-shadow:0 5px 4px rgba(0,0,0,0.12),0 5px 7px rgba(0,0,0,0.24);transition:all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1)}@media (min-width: 767px){div.glasses_wrapper>div.glasses_list>div.item{width:300px;height:300px}}@media (min-width: 1400px){div.glasses_wrapper>div.glasses_list>div.item{width:350px;height:350px}}div.glasses_wrapper>div.glasses_list>div.item>div.item_top{display:flex;width:100%;border-radius:4px 4px 0 0;padding:1em 0 0.5em 0;justify-content:flex-end;position:relative}div.glasses_wrapper>div.glasses_list>div.item>div.item_top>span.discount{display:flex;justify-content:center;align-items:center;position:absolute;left:0;background-color:#f22a42;color:white;font-weight:bold;font-size:1em;border-radius:0 1em 1em 0;padding:2px 15px 2px 10px}div.glasses_wrapper>div.glasses_list>div.item>div.item_top>span.qty{display:flex;align-items:center;color:#f22a42;padding:0 10px}div.glasses_wrapper>div.glasses_list>div.item>div.item_top>span.qty>.fire_icon{margin-left:5px}div.glasses_wrapper>div.glasses_list>div.item>div.glasses_photo{height:40%;width:100%}@media (min-width: 767px){div.glasses_wrapper>div.glasses_list>div.item>div.glasses_photo{height:45%}}div.glasses_wrapper>div.glasses_list>div.item>span.name{width:100%;font-weight:bold;font-size:1.2em;margin:0.3em 0;color:#0e2544}div.glasses_wrapper>div.glasses_list>div.item>p.price_and_shipping{margin:0;display:flex;justify-content:center;align-items:center;color:#0e2544}div.glasses_wrapper>div.glasses_list>div.item>p.price_and_shipping>span.price{margin-right:0.5em;display:flex;position:relative}div.glasses_wrapper>div.glasses_list>div.item>p.price_and_shipping>span.price>span.line{position:absolute;width:100%;border-top:thin solid #0e2544;top:57%}div.glasses_wrapper>div.glasses_list>div.item>p.price_and_shipping>span.final_price{margin-right:0.15em;font-weight:bold;font-size:1.15em;color:#f22a42}div.glasses_wrapper>div.glasses_list>div.item>a{display:flex;width:80%;margin-top:0.2em}@media (min-width: 767px){div.glasses_wrapper>div.glasses_list>div.item>a{margin-top:0.5em}}div.glasses_wrapper>div.glasses_list>div.item>a>button.item_btn{color:white;background-color:#FF9500;border:thin solid #FF9500;font-weight:bold;padding:3px;width:100%;border-radius:30px;margin:5px 0 0 0;font-size:0.8em}@media (min-width: 767px){div.glasses_wrapper>div.glasses_list>div.item>a>button.item_btn{font-size:1em}}@media (min-width: 1400px){div.glasses_wrapper>div.glasses_list>div.item>a>button.item_btn{margin-top:10px}}div.glasses_wrapper>div.glasses_list>div.item>a>button.item_btn:hover{background-color:#2196f3;border:thin solid #2196f3}div.glasses_wrapper>div.glasses_list>div.item>a>button.disabled{color:gray;border:thin solid gray;background-color:white}div.glasses_wrapper>div.glasses_list>div.item_darkMode{background-color:#0e2544}div.glasses_wrapper>div.glasses_list>div.item_darkMode>div.item_top{background-color:white}div.glasses_wrapper>div.glasses_list>div.item_darkMode>span.name{color:#2196f3}div.glasses_wrapper>div.glasses_list>div.item_darkMode>a button.disabled{background-color:#0e2544}div.glasses_wrapper>div.glasses_list>div.item_darkMode>p.price_and_shipping{margin:0;display:flex;justify-content:center;align-items:center;color:white}div.glasses_wrapper>div.glasses_list>div.item_darkMode>p.price_and_shipping>span.price span.line{border-top:thin solid white}div.glasses_wrapper>div.glasses_list>div.item:hover{box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22)}div.glasses_wrapper>div.darkMode_list{background-color:#0e2544}

div.App{text-align:center;background-color:white;width:100%;height:100vh;display:flex;flex-wrap:wrap;flex-direction:column;align-items:center;position:static;font-family:'Roboto', sans-serif}div.app_darkMode{background-color:#0e2544}@-webkit-keyframes g_color_background{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes g_color_background{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}

